<template><section><h1>BoxGeometry, BoxOutlineGeometry</h1>
<ul>
<li>The <code v-pre>vc-geometry-box</code> component is used to load a box geometry that describes a cube centered at the origin.</li>
<li>The <code v-pre>vc-geometry-outline-box</code> component is used to load a box geometry that describes the outline of a cube centered at the origin.</li>
<li>It needs to be used as a sub-component of <code v-pre>vc-instance-geometry</code>, can be mounted on <code v-pre>vc-primitive</code> or <code v-pre>vc-primitive-ground</code>.</li>
</ul>
<h2>Example</h2>
<h3>Load BoxGeometry</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @LEFT_CLICK="LEFT_CLICK">
        <vc-primitive @click="clicked" :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes" :modelMatrix="modelMatrix">
            <vc-geometry-box ref="box" :dimensions="dimensions"></vc-geometry-box>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes" :modelMatrix="modelMatrix2">
            <vc-geometry-outline-box ref="boxOutline" :dimensions="dimensions"></vc-geometry-outline-box>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
