<h2>Instance Properties</h2>
<h3><code v-pre>vc-geometry-box</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimensions</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> The width, depth, and height of the box stored in the x, y, and z coordinates of the Cartesian3, respectively. <strong>structure: { x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The vertex attributes to be computed.</td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre>vc-geometry-outline-box</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimensions</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> The width, depth, and height of the box stored in the x, y, and z coordinates of the Cartesian3, respectively. <strong>structure: { x: number, y: number, z: number }</strong></td>
</tr>
</tbody>
</table>
<hr>
<p>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/BoxGeometry.html">BoxGeometry</a></strong>, <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/BoxOutlineGeometry.html">BoxOutlineGeometry</a></strong></p>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          attributes: null,
          geometry: null,
          modelMatrix: null,
          modelMatrix2: null,
          dimensions: { x: 400000.0, y: 300000.0, z: 500000.0 }
        }
      },
      mounted() {
        Promise.all([this.$refs.box.createPromise, this.$refs.boxOutline.createPromise]).then((instances) => {
          console.log('All geometries are loaded.')
          const { BoundingSphere } = Cesium
          const boundingSphereUnion = instances.reduce((prev, cur) => {
            const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            const boundingSphere = BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
            return prev === null ? boundingSphere : BoundingSphere.union(prev, boundingSphere)
          }, null)
          instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.viewer = viewer
          const { ColorGeometryInstanceAttribute, PerInstanceColorAppearance, Matrix4, Cartesian3, Transforms } = Cesium
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random(), 0.5)
          }
          this.appearance = new PerInstanceColorAppearance({
            flat: true
          })
          this.modelMatrix = Matrix4.multiplyByTranslation(
            Transforms.eastNorthUpToFixedFrame(Cartesian3.fromDegrees(105.0, 40.0)),
            new Cartesian3(0.0, 0.0, 250000),
            new Matrix4()
          )
          this.modelMatrix2 = Matrix4.multiplyByTranslation(
            Transforms.eastNorthUpToFixedFrame(Cartesian3.fromDegrees(110.0, 40.0)),
            new Cartesian3(0.0, 0.0, 250000),
            new Matrix4()
          )
        },
        LEFT_CLICK(movement) {
          const feature = this.viewer.scene.pick(movement.position)
          console.log(feature)
        },
        clicked(e) {
          console.log(e)
        }
      }
    }
  </script>